import { Directive, ElementRef, forwardRef, Inject } from '@angular/core';
import { NwDropdownAnchor } from './nw-dropdown-anchor.directive';
import { NwDropdown } from './nw-dropdown.directive';

/**
 * A directive to mark an element that will toggle dropdown via the `click` event.
 *
 * You can also use `NwDropdownAnchor` as an alternative.
 */
@Directive({
  selector: '[nwDropdownToggle]',
  host: {
    'class': 'dropdown-toggle',
    'aria-haspopup': 'true',
    '[attr.aria-expanded]': 'dropdown.isOpen()',
    '(click)': 'dropdown.toggle()',
    '(keydown.ArrowUp)': 'dropdown.onKeyDown($event)',
    '(keydown.ArrowDown)': 'dropdown.onKeyDown($event)',
    '(keydown.Home)': 'dropdown.onKeyDown($event)',
    '(keydown.End)': 'dropdown.onKeyDown($event)'
  },
  providers: [{ provide: NwDropdownAnchor, useExisting: forwardRef(() => NwDropdownToggle) }]
})
export class NwDropdownToggle extends NwDropdownAnchor {

  constructor(
    @Inject(forwardRef(() => NwDropdown)) dropdown,
    elementRef: ElementRef<HTMLElement>
  ) {
    super(dropdown, elementRef);
  }

}
