import { Directive, ElementRef, forwardRef, Inject } from '@angular/core';
import { NwDropdown } from './nw-dropdown.directive';

/**
 * A directive to mark an element to which dropdown menu will be anchored.
 *
 * This is a simple version of the `NwDropdownToggle` directive.
 * It plays the same role, but doesn't listen to click events to toggle dropdown menu thus enabling support
 * for events other than click.
 *
 * @since 1.1.0
 */
@Directive({
  selector: '[nwDropdownAnchor]',
  host: {
    'class': 'dropdown-toggle',
    'aria-haspopup': 'true',
    '[attr.aria-expanded]': 'dropdown.isOpen()'
  }
})
export class NwDropdownAnchor {

  anchorEl;

  constructor(
    @Inject(forwardRef(() => NwDropdown)) public dropdown,
    private _elementRef: ElementRef<HTMLElement>
  ) {
    this.anchorEl = _elementRef.nativeElement;
  }

  getNativeElement() { return this._elementRef.nativeElement; }

}
