import { ContentChildren, Directive, forwardRef, Inject, QueryList } from '@angular/core';
import { Placement } from '../util/positioning.util';
import { NwDropdownItem } from './nw-dropdown-item.directive';
import { NwDropdown } from './nw-dropdown.directive';

/**
 * A directive that wraps dropdown menu content and dropdown items.
 */
@Directive({
  selector: '[nwDropdownMenu]',
  host: {
    '[class.dropdown-menu]': 'true',
    '[class.show]': 'dropdown.isOpen()',
    '[attr.x-placement]': 'placement',
    '(keydown.ArrowUp)': 'dropdown.onKeyDown($event)',
    '(keydown.ArrowDown)': 'dropdown.onKeyDown($event)',
    '(keydown.Home)': 'dropdown.onKeyDown($event)',
    '(keydown.End)': 'dropdown.onKeyDown($event)',
    '(keydown.Enter)': 'dropdown.onKeyDown($event)',
    '(keydown.Space)': 'dropdown.onKeyDown($event)'
  }
})
export class NwDropdownMenu {

  placement: Placement = 'bottom';
  isOpen = false;

  @ContentChildren(NwDropdownItem) menuItems: QueryList<NwDropdownItem>;

  constructor(@Inject(forwardRef(() => NwDropdown)) public dropdown) { }

}
